import http from '@/api/http.js'
export default{
	data(){
		return{
			goodsTeam:[]
		}
	},
	onLoad(){
		this.getGoodsTeam()
	},
	methods:{
		getGoodsTeam(){
			let data = {
					token:uni.getStorageSync('token'),
					type:2
				}
			http('/api/user/customer','post',data).then(res=>{
				if(res.data.status == '200'){
					this.goodsTeam = res.data.data
				}
				console.log(res)
			})
		}
	}
}
  
